<template>
  <div class="macDetail">
    <div class="devicetype">
      <p class="t">{{titleName}} <span @click="handleBack">返回</span></p>
    </div>
    <div v-if="deviceType == 2 || deviceType == 5 || deviceType == 7 || deviceType == 26 || deviceType == 24 || deviceType == 15 || deviceType == 'patrol'"><device-detail-common :deviceInfo="deviceInfo" :alarmHistory="alarmHistory" :binder_phone="binder_phone" :binder_name="binder_name"></device-detail-common></div>
    <div v-if="deviceType == 21"><device-detail-electro :deviceInfo="deviceInfo" :alarmHistory="alarmHistory" :binder_phone="binder_phone" :binder_name="binder_name"></device-detail-electro></div>
    <div v-if="deviceType == 23"><device-detail-water :deviceInfo="deviceInfo" :alarmHistory="alarmHistory" :binder_phone="binder_phone" :binder_name="binder_name"></device-detail-water></div>
  </div>
</template>

<script>
import { findDevice, deviceAlarmList, findband } from '@/api/device'
import deviceDetailCommon from './components/deviceDetailCommon'
import deviceDetailElectro from './components/deviceDetailElectro'
import deviceDetailWater from './components/deviceDetailWater'

export default {
  name: 'MacDetail',
  components: {
    deviceDetailCommon,
    deviceDetailElectro,
    deviceDetailWater
  },
  mounted() {
    this.deviceInfo.mac = this.$route.params.mac
    this.findDevice()
    this.queryAlarms()
    this.findband()
    console.log(this.$route.params)
  },
  data() {
    return {
      deviceInfo: {
      },
      alarmHistory: [],
      supervisor: [],
      binder_name: '此设备无监管人',
      binder_phone: ''
    }
  },
  computed: {
    titleName() {
      return `${this.$route.query.name}设备详情` || '设备详情'
    },
    deviceType() {
      return this.$route.params.type || '2'
    }
  },
  methods: {
    handleBack() {
      this.$router.back()
    },
    async findDevice() {
      const mac = this.deviceInfo.mac || this.$route.params.mac
      const { data } = await findDevice(mac)
      this.deviceInfo = data
      console.log(data)
    },
    async queryAlarms() {
      const mac = this.deviceInfo.mac || this.$route.params.mac
      const { data } = await deviceAlarmList(mac)
      this.alarmHistory = data
      console.log(data)
    },
    async findband() {
      const mac = this.deviceInfo.mac || this.$route.params.mac
      const { data } = await findband(mac)
      this.supervisor = data
      this.binder_name = this.supervisor[0].supervisor_name
      this.binder_phone = this.supervisor[0].supervisor_account
      for (let i = 0; i < this.supervisor[1].length; i++) {
        const binder = { name: this.supervisor[1][i].update_supervisor_name + this.supervisor[1][i].update_supervisor_account, code: this.supervisor[1][i].update_supervisor_account }
        this.binder_list.push(binder)
      }
      console.log(data)
    }


  }
}
</script>

<style lang="less" scoped>
  .devicetype{
    margin-bottom: 20px;
    .t {
      font-size: 15px;
      color: #00ffc3;
      line-height: 50px;
      border-bottom: 1px solid #15a2eb;
      span {
        float: right;
        cursor: pointer;
      }
    }
  }
</style>
