<template>
  <div class="deviceDetailElectro deviceDetailCommon">
    <div class="info">
      <div class="baseinfo">
        <div class="second-title">基本信息</div>
        <div class="second-container">
          <p class="attr">
            <span class="k">设备ID</span>
            <span class="v">{{deviceInfo.mac}}</span>
          </p>
          <p class="attr">
            <span class="k">手机号</span>
            <span class="v">{{deviceInfo.phone}}</span>
          </p>
          <p class="attr">
            <span class="k">监管人</span>
            <span class="v">{{binder_name}}&nbsp;{{binder_phone}}</span>
          </p>
          <p class="attr">
            <span class="k">安装高度</span>
            <span class="v">{{deviceStatus.install_height}} 米</span>
          </p>
          <p class="attr">
            <span class="k">安装位置</span>
            <span class="v">{{deviceInfo.address}}/{{deviceInfo.alias}}/{{deviceInfo.building}}/{{deviceInfo.floor}}/{{deviceInfo.room}}</span>
          </p>
        </div>
      </div>
      <div class="alarmsinfo">
        <div class="second-title">报警记录</div>
        <div class="second-container">
          <div v-if="alarmHistory.length === 0" class="empty-data">
            <span class="iconfont iconzan"></span>
            <p>设备运行良好，未发现异常报警</p>
          </div>
        </div>
      </div>
    </div>
    <div class="info">
      <div class="baseinfo">
        <div class="second-title">水位状态</div>
        <div class="second-container dataimg">
          <img src="@/assets/img/swy.png" class="ldwd-bg" alt="">
        </div>
      </div>
      <div class="alarmsinfo">
        <div class="second-title">水位监控</div>
        <div class="second-container">
        </div>
      </div>
    </div>
    <div class="info">
      <div class="baseinfo">
        <div class="second-title">主要配置参数<span class="save" @click="saveConfig">保存</span></div>
        <div class="lists">
          <el-scrollbar style="height: 100%;">
            <div class="second-container has-config">
              <div class="config-item">
                <p class="c-title">水位报警门限值（米）</p>
                <p class="c-val">
                  <input type="text" v-model="config.val">
                  <span>*安装设备时，通过APP端设置</span>
                </p>
              </div>
              <div class="config-item">
                <p class="c-title">静默期（h）</p>
                <p class="c-val">
                  <input type="text" v-model="config.interval">
                  <span>*请确保静默期内可以解除异常状态</span>
                </p>
              </div>
            </div>
          </el-scrollbar>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import msgUtil from '@/utils/msgUtil'
import dateUtil from '@/utils/dateUtil'
import { deviceConfig, deviceStatus, logHistory, deviceAlarmList } from '@/api/device'

export default {
  name: 'DeviceDetailWater',
  props: ['deviceInfo', 'alarmHistory', 'binder_name', 'binder_phone'],
  data() {
    return {
      deviceStatus: {},
      config: {
        val: undefined,
        interval: undefined
      }
    }
  },
  methods: {

    init(mac, phone, address) {
      this.deviceInfo = {
        mac,
        phone,
        address
      }
      // 实时监控
      deviceStatus(mac).then(resp => {
        if (mac === this.deviceInfo.mac) {
          this.deviceStatus = resp.data || {}
          this.setChartView()
          if (this.deviceStatus.alarm_val !== this.config.val) {
            this.config.val = this.deviceStatus.alarm_val
          }
          if (this.deviceStatus.alarm_interval !== this.config.interval) {
            this.config.interval = this.deviceStatus.alarm_interval
          }
        }
      }, error => {
        msgUtil.error('获取设备状态失败', msgUtil.findErrorMsg(error))
      })
      // 基本信息
      // 监控图
      logHistory(this.deviceInfo.mac).then(resp => {
        if (mac === this.deviceInfo.mac) {
          const time = []
          const series = [{ name: '水位', data: [] }]
          resp.data.forEach(i => {
            time.push(dateUtil.format(new Date(i.create_time * 1000), 'YYYY-MM-dd HH:mm:ss'))
            series[0].data.push(i.val)
          })
          if (!this.wl_chart) {
            this.wl_chart = this.$echarts.init(document.getElementById('wl_chart'))
          }
          this.wl_chart.setOption(this.$chartUtil.lineChartOpt(time, ['水位'], series, true))
        }
      }, error => {
        msgUtil.error('获取监控信息失败', msgUtil.findErrorMsg(error))
      })
      // 报警记录
      deviceAlarmList(this.deviceInfo.mac).then(resp => {
        if (mac === this.deviceInfo.mac) {
          // 更新报警状态
          if (resp.data.length > this.alarmHistory.length) {
            this.deviceInfo.alarmState = true // 设备已报警
            this.time = dateUtil.formatCh(resp.data[0].time)
          }
          this.alarmHistory = resp.data.map(a => {
            const detail = a.detail
            return `${dateUtil.formatCh(a.time)}，水位仪发生水位不足报警, 报警值为${detail.level}, 请及时补水`
          })
        }
      }, error => {
        msgUtil.error('获取设备告警失败', msgUtil.findErrorMsg(error))
        clearInterval(this.timer)
      })
    },
    async saveConfig() {
      if (!this.deviceInfo.mac) {
        msgUtil.warning('保存失败', '请选择配置设备！')
        return
      }

      if (this.config.val >= 0 && this.config.interval >= 0) {
        deviceConfig(this.deviceInfo.mac, {
          alarm_val: this.config.val - 0,
          alarm_interval: this.config.interval - 0
        }).then(success => {
          msgUtil.success('保存成功')
        }, error => {
          msgUtil.error('保存失败', msgUtil.findErrorMsg(error))
        })
      } else {
        msgUtil.warning('保存失败', '请输入正确的报警门限值和静默期值！')
      }
    }
  }
}
</script>

