<template>
  <div class="deviceDetailCommon">
    <div class="map" id="device-point-map">

    </div>
    <div class="info">
      <div class="baseinfo">
        <div class="second-title">基本信息</div>
        <div class="second-container">
          <p class="attr">
            <span class="k">设备ID</span>
            <span class="v">{{deviceInfo.mac}}</span>
          </p>
          <p class="attr" v-if="deviceInfo.type == 26">
            <span class="k">使用人</span>
            <span class="v">{{deviceInfo.user}}</span>
          </p>
          <p class="attr" v-if="deviceInfo.type == 26">
            <span class="k">年龄</span>
            <span class="v">{{deviceInfo.age}}</span>
          </p>
          <p class="attr">
            <span class="k">监管人</span>
            <span class="v">{{binder_name}}&nbsp;{{binder_phone}}</span>
          </p>
          <p class="attr">
            <span class="k">手机号</span>
            <span class="v">{{deviceInfo.phone}}</span>
          </p>
          <p class="attr">
            <span class="k">安装位置</span>
            <span class="v">{{deviceInfo.address}}/{{deviceInfo.alias}}/{{deviceInfo.building}}/{{deviceInfo.floor}}/{{deviceInfo.room}}</span>
          </p>
        </div>
      </div>
      <div class="alarmsinfo">
        <div class="second-title">报警记录</div>
        <div class="second-container">
          <div v-if="alarmHistory.length === 0" class="empty-data">
            <span class="iconfont iconzan"></span>
            <p>设备运行良好，未发现异常报警</p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
// import $ from 'jquery'
import { nbinspectiongps } from '@/api/device'
import msgUtil from '@/utils/msgUtil'

export default {
  name: 'DeviceDetailCommon',
  props: ['deviceInfo', 'alarmHistory', 'binder_name', 'binder_phone'],
  mounted() {
    this.initMap()
  },
  data() {
    return {
      map: undefined
    }
  },
  methods: {
    initMap() {
      this.map = new BMap.Map('device-point-map')
      console.log(this.map)
      this.setMapPoint(this.deviceInfo.lng, this.deviceInfo.lat)
    },
    setMapPoint(lng, lat) {
      this.map.clearOverlays()
      const point = new BMap.Point(lng, lat)
      this.map.centerAndZoom(point, 13)
      this.map.enableScrollWheelZoom();
      const icon = new BMap.Icon('../../../assets/img/icon1.png', new BMap.Size(25, 25))
      const marker = new BMap.Marker(point, { icon: icon })
      this.map.addOverlay(marker)
      marker.setAnimation(BMAP_ANIMATION_BOUNCE)

      // 两点之间直线
      if (this.deviceInfo.type == 26) {
        var detail = {
          start: Math.ceil(this.mapqueryFilter.start.getTime() / 1000),
          end: Math.ceil(this.mapqueryFilter.end.getTime() / 1000),
          mac: this.queryFilter.mac,
          page: 1,
          per_page: 9999999999999999999
        }
        // 创建polyline对象
        var pois = []
        nbinspectiongps(detail).then(resp => {
          if (resp.status == 200) {
            this.mapdata = resp.data.items
            var maplength = this.mapdata.length
            if (!maplength) {
              msgUtil.error('该时间段内无巡更记录！')
              const point = new BMap.Point(lng, lat)
              return false
            }
            for (var i = maplength - 1; i >= 0; i--) {
              pois.push(new BMap.Point(this.mapdata[i].longitude * 1, this.mapdata[i].latitude * 1))
            }
            var satrtMarker = null
            var endMarker = null
            var polyline = null
            var map = this.map
            // Array.prototype.push.apply(bdpoints,data.points);
            if (pois) {
              if (satrtMarker && endMarker && polyline) {
                map.removeOverlay(satrtMarker)
                map.removeOverlay(endMarker)
                map.removeOverlay(polyline)
              }
              var sy = new BMap.Symbol(BMap_Symbol_SHAPE_BACKWARD_OPEN_ARROW, {
                scale: 0.1, // 图标缩放大小
                strokeOpacity: 0,
                strokeColor: '#094f9e', // 设置矢量图标的线填充颜色
                strokeWeight: '1' // 设置线宽
              });
              var icons = new BMap.IconSequence(sy, '10', '30');
              polyline = new BMap.Polyline(pois, {
                enableEditing: false, // 是否启用线编辑，默认为false
                enableClicking: true, // 是否响应点击事件，默认为true
                // icons:[icons],
                strokeWeight: '6', // 折线的宽度，以像素为单位
                strokeOpacity: 0.8, // 折线的透明度，取值范围0 - 1
                strokeColor: '#094f9e' // 折线颜色
              });
              if (maplength == pois.length) {
                satrtMarker = new BMap.Marker(pois[0], { icon: new BMap.Icon('./static/img/start.png', new BMap.Size(30, 50)) })
                endMarker = new BMap.Marker(pois[pois.length - 1], { icon: new BMap.Icon('./static/img/end.png', new BMap.Size(30, 50)) })
                map.addOverlay(satrtMarker)
                map.addOverlay(endMarker)
                map.removeOverlay(marker)
                map.addOverlay(polyline); // 增加折线
                const point = pois[maplength - 1]
                map.centerAndZoom(point, 16)
              }
            }
          }
        }).catch(e => {
          msgUtil.error(msgUtil.findErrorMsg(e))
        })
      }
    }
  }
}
</script>


<style lang="less" scoped>
@import url('./common.less');
</style>
