        </div>
      </div>
      <div class="alarmsinfo">
        <div class="second-title">剩余电流监控数据</div>
        <div class="second-container">
        </div>
      </div>
    </div>
    <div class="info">
      <div class="baseinfo">
        <div class="second-title">温度监控数据</div>
        <div class="second-container dataimg">
        </div>
      </div>
      <div class="alarmsinfo more-info">
        <div class="second-title">主要配置参数 <span class="save" @click="saveConfig">保存</span></div>
        <div class="lists">
          <el-scrollbar style="height: 100%;">
            <div class="second-container has-config">
              <div class="config-item">
                <p class="c-title">漏电门限值（mA）</p>
                <p class="c-val">
                  <input type="text" v-model="config.alarm_val1">
                  <span>*请输入整数的报警门限值（单位mA）</span>
                </p>
              </div>
              <div class="config-item">
                <p class="c-title">1路温度门限值（°C）</p>
                <p class="c-val">
                  <input type="text" v-model="config.alarm_val2">
                  <span>*请输入整数的报警门限值（单位°C）</span>
                </p>
              </div>
              <div class="config-item">
                <p class="c-title">2路温度门限值（°C）</p>
                <p class="c-val">
                  <input type="text" v-model="config.alarm_val3">
                  <span>*请输入整数的报警门限值（单位°C）</span>
                </p>
              </div>
              <div class="config-item">
                <p class="c-title">3路温度门限值（°C）</p>
                <p class="c-val">
