                  <span>*请输入整数的报警门限值（单位°C）</span>
                </p>
              </div>
              <div class="config-item">
                <p class="c-title">4路温度门限值（°C）</p>
                <p class="c-val">
                  <input type="text" v-model="config.alarm_val5">
                  <span>*请输入整数的报警门限值（单位°C）</span>
                </p>
