<script>
import msgUtil from '@/utils/msgUtil'
import { deviceConfig } from '@/api/device'

export default {
  name: 'DeviceDetailElectro',
  props: ['deviceInfo', 'alarmHistory', 'binder_name', 'binder_phone'],
  data() {
    return {
      config: {
        alarm_val1: 0,
        alarm_val2: 0,
        alarm_val3: 0,
        alarm_val4: 0,
        alarm_val5: 0
      }
    }
  },
  methods: {
    async saveConfig() {
      if (!this.deviceInfo.mac) {
        msgUtil.warning('保存失败', '请选择配置设备！')
        return
      }
      if (this.config.alarm_val1 >= 0 && this.config.alarm_val2 >= 0 && this.config.alarm_val3 >= 0 && this.config.alarm_val4 >= 0 && this.config.alarm_val5 >= 0) {
        if (Math.ceil(this.config.alarm_val1) != this.config.alarm_val1 || Math.ceil(this.config.alarm_val2) != this.config.alarm_val2 || Math.ceil(this.config.alarm_val3) != this.config.alarm_val3 || Math.ceil(this.config.alarm_val4) != this.config.alarm_val4 || Math.ceil(this.config.alarm_val5) != this.config.alarm_val5) {
          msgUtil.error('输入错误', '请输入整数的报警门限值')
          return
        }
        try {
          const params = {
            alarm_val1: this.config.alarm_val1 - 0,
            alarm_val2: this.config.alarm_val2 - 0,
            alarm_val3: this.config.alarm_val3 - 0,
            alarm_val4: this.config.alarm_val4 - 0,
            alarm_val5: this.config.alarm_val5 - 0
          }
          await deviceConfig(this.deviceInfo.mac, params)
          msgUtil.success('保存成功')
        } catch (error) {
          msgUtil.error('保存失败', msgUtil.findErrorMsg(error))
        }
      } else {
        msgUtil.warning('保存失败', '请输入正确的报警门限值！')
      }
    }
  }
}
</script>

